import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faGithub } from '@fortawesome/free-brands-svg-icons'
import { faInstagram } from '@fortawesome/free-brands-svg-icons'
import React from 'react'

const About = () => {
    return (
        <div className="font-regular text-4xl container text-center mx-auto h-full mb-auto">
            <h1><a target="__blank" href="https://instagram.com/"><FontAwesomeIcon icon={faInstagram} /></a><a target="__blank" href="https://github.com/ajwhitney23"><FontAwesomeIcon icon={faGithub} /></a></h1>
        </div>
    )
}

export default About
