import React from 'react'
import Typical from 'react-typical'
//slideshow
const Home = () => {
    return (
        <div className="container mx-auto font-regular text-4xl">
            <Typical
                className="text-center"
                steps={['Hello!', 1000, 'Click Projects to learn more about my projects!', 1000, 'Click About to learn more about me!', 1000,
                        'Click Contact to get my contact info!', 1000]}
                loop={Infinity}
                wrapper="h1"
            />
        </div>
    )
}

export default Home
